import React from 'react'
import { Field, reduxForm } from 'redux-form'

import e from '../Header.module.css'

import { Input } from '../../../common/FormControl/FormControl'
import { required } from '../../../common/validators/validator'

const Login = ({ handleSubmit, error}) => {
	return (
		<form onSubmit={handleSubmit}>
			<div>
				<label>Login</label>
				<div>
					<Field
						name='login'
						component={Input}
						type='text'
						placeholder='email'
						validate={[required]}
					/>
				</div>
			</div>
			<div>
				<label>Password</label>
				<div>
					<Field
						name='password'
						component={Input}
						type='password'
						placeholder='password'
						validate={[required]}
					/>
				</div>
			</div>
			{error && <strong className={e.error}>{error}</strong>}
			<div>
				<button type='submit'>Submit</button>
			</div>
		</form>
	)
}

let LoginReduxForm = reduxForm({
	form: 'adminLogin',
})(Login)

export default LoginReduxForm
