let initialState = {
	isAuth: false,
	creatorLogin: {
		login: 'qwerty',
		password: '123',
	},
}

let shopItemReducer = (state = initialState, action) => {
	switch (action.type) {
		case 'isLogined': {
			return {
				...state,
				isAuth:
					state.creatorLogin.login === action.log.login &&
					state.creatorLogin.password === action.log.password &&
					true,
			}
		}
		case 'notLogined': {
			return {
				...state,
				isAuth: false,
			}
		}
		default:
			return state
	}
}

export const logIn = (log) => ({ type: 'isLogined', log })

export const logOut = () => ({ type: 'notLogined' })

export default shopItemReducer
