import React from 'react'
import { SubmissionError } from 'redux-form'

import h from './Header.module.css'

import logo from '../../common/img/header/logo.png'
import LoginModal from '../ModalWindow/LoginModal'

const Header = (props) => {

	let changeAuth = () => {
		props.logOut()
	}
	let onSubmit =(data) => {
		if (!'qwerty'.includes(data.username) && data.password !== '123')  {
			throw new SubmissionError({
			  _error: 'Login or password wrong!'
			})
		  }  else {
			props.logIn(data)
		  }
		
	}
	console.log(props.isAuth)
	return (
		<header className={h.header}>
				<div className={h.logo}><img src={logo} alt="logo" /></div>
				<div>Welcome to my site</div> 
				<div>{props.isAuth ? <button onClick={changeAuth}>Logout</button> : <LoginModal onSubmit={onSubmit}/>  }</div>
				
		</header>
	)
}
export default Header
