import React, { useState } from 'react'
import Modal from 'react-modal'
import LoginReduxForm from '../Header/LoginForm/LoginForm'

let LoginModal = (props) => {
	const [modal, setModal] = useState(false)

	let openModal = () => {
		setModal(true)
	}

	let closeModal = () => {
		setModal(false)
	}

	const customStyles = {
		content: {
			top: '35%',
			left: '50%',
			right: 'auto',
			bottom: 'auto',
			marginRight: '-50%',
			width: '20%',
			transform: 'translate(-40%, -10%)',
		},
	}
	return (
		<div>
			<div >
				<button onClick={openModal}>Login</button>
			</div>

			<Modal style={customStyles} isOpen={modal} onRequestClose={closeModal} ariaHideApp={false}>
				<LoginReduxForm onSubmit={props.onSubmit} closeModal={closeModal} />
			</Modal>
			
		</div>
	)
}

export default LoginModal
